  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --secondary-purple: #3D4267;
  --primary-gray: #292929;
  --white: #E8E9F3;
  --medium-gray: #525252;
  --light-purple: #8E98E9;
  --light-gray: #C4C4C4;
}
/* OVERALL */

body {
	background-color: #525252;
	background-color: var(--medium-gray);
	font-family: 'Questrial', sans-serif;
}

h1 {
	font-size: 28pt;
}

h1, h2, h3, h4 {
	font-weight: 800;
	font-family: 'Work Sans', sans-serif;
}

p, a {
	font-size: 14pt;
}

/* LINKS */

a {
	text-decoration: none;
	font-weight: 600;
	cursor: pointer;
}

.strike {
	color: #E8E9F3;
	color: var(--white);
	position: relative;
	line-height: 30px;
}

.strike:hover {
	cursor: pointer;
	-webkit-animation: strikethrough;
	        animation: strikethrough;
	color: #E8E9F3;
	color: var(--white);
}

@-webkit-keyframes strikethrough {
	0% {
		-webkit-transform: scaleX(0);
		        transform: scaleX(0);
	}
	100% {
		-webkit-transform: scaleX(1);
		        transform: scaleX(1);
	}
}

@keyframes strikethrough {
	0% {
		-webkit-transform: scaleX(0);
		        transform: scaleX(0);
	}
	100% {
		-webkit-transform: scaleX(1);
		        transform: scaleX(1);
	}
}

.strike:hover:after {
  content: ' ';
  position: absolute;
  display: block;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  background: #E8E9F3;
  background: var(--white);
  -webkit-transform-origin: 0% 50%;
          transform-origin: 0% 50%;
  -webkit-animation: strikethrough .3s ease-out;
          animation: strikethrough .3s ease-out;
}

.strike-purple {
	color: #3D4267;
	color: var(--secondary-purple);
}

.strike-purple:hover:after {
	background-color: #3D4267;
	background-color: var(--secondary-purple);
}

.flex-row {
	display: flex;
	flex-direction: row;
}

@media only screen and (max-width: 767px) {
	p, a {
		font-size: 12pt;
	}
}

